
.next-gen.zpore.is-slicing {
    .slice-recorder, .simple-controls {
        display: none;
    }
}

.next-gen.zpore.showing-remixes {
    .simple-controls, .connect-button-wrapper {
        display: none;
    }
}

.next-gen.zpore.hide-ui {

    .spore-embed-helper {
        display: none;
    }

    .zs-container {
        display: none;
    }

    .nft-jacker {
    }
}

.zpore.connected {
    .nft-jacker {
        z-index: unset !important;
    }
    .drop-top {
        display: block;
        z-index: 1000000000 !important;
    }
}
.inner-overlay {
    .click-text {
        button.enter-btn {
            border-radius: 8px;
            padding: 8px;
            padding-left: 15px;
            padding-right: 15px;
        }
        button {
            margin-left: auto;
            margin-right: auto;
            background-color: #121212;
            div {

                background: linear-gradient(60deg, #03fe18 0%, #85ff75e0 25%, rgb(60 220 238) 50%, #ffbdec 75%, #eeb2d8 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            display: inline-block;
            -webkit-mask:linear-gradient(-60deg,#000 30%,#0005,#000 70%) right/300% 100%;
            background-repeat: no-repeat;
            animation: shimmer 2.5s infinite;
            }
        }
    }
}
 
.zpore.next-gen {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

   .nft-jacker.drop.thugger {
        .spore-embed-container {
            .spore-embed-helper {
                margin-top: -20px;
                font-size: 10pt;
                .song-title {
                    color: white;
                }

                .song-artist {
                    color: #d5d1d1;
                }
            }
        }
    }

    .simple-controls {
        position: absolute;
        bottom: 45px;
        left: 45px;
        z-index: 10000000000;

        .play-btn-wrapper {
            border: none;
            background: none;
            cursor: pointer;
        }
    }
    .drop.nft-jacker.jacked.thugger  {
        padding-bottom: unset;
        overflow-y: unset !important;
        .drop-description {
            //display: block !important;
        }
    }
    .drop-top {
        //display: none;
    }
}

@media screen and (max-width: 600px) {
    .next-gen.zpore {
        .simple-controls {
            position: absolute;
            bottom: 0px;
            left: unset;
            right: -15px;
        }
    }
}

