.stems-portal {
    display: flex;
    flex-direction: column;
    overflow: scroll;
    height: 100%;
    background: linear-gradient(5deg, hsl(314,39.6%,6.24%) 0%, #08102b 50%, #251020 100%);

    .completed-info {
        position: absolute;
        top: 10px;
        left: 10px;
        background-color: #11ff20;
        color: black;
        padding: 10px;
        padding-right: 40px;

        .close-btn {
            position: absolute;
            top: 3px;
            right: 10px;
            cursor: pointer;
        }
    }

    .song-key.zora-field {
        .field-name {
            position: unset;
        }
        height: unset;
    }
    .Dropdown-control {
        width: 120px;
        font-size: 9pt;
    }
    .open-in-spores {
        margin-left: auto;
        padding: 5px 10px 5px 10px;
        color: white;
        background-color: red;
        align-items: flex-start;
        border-radius: 8px;
        margin-bottom: 10px;
    }
    .layout {
        flex-direction: column;
        display: flex;
        margin: 100px auto 100px auto;
        width: 75%;

        .banner-image {
            width: 15%;
            position: absolute;
            z-index: 0;
            top: 20px;
            right: 20px;
        }

        .stem-types {
            margin-left: 10px;
            display: flex;
            
            .stem-type.selected {
                background-image: linear-gradient(45deg, #5bf3cb, #07ff00);
                color: black;
            }
            .stem-type {
                font-size: 9pt;
                margin-right: 5px;
                cursor: pointer;
                height: 20px;
                padding-left: 10px;
                padding-right: 10px;
                line-height: 20px;
                border-radius: 12px;
                background-color: #2b2b2b;
            }
        }
        
        .f-row {
            margin-top: 30px;
            display: flex;
        }

        .song-stems {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            .song-stem.is-not-loop {
                background-color: #290c5f;
            }
            .song-stem {
                font-size: 9pt;
                display: flex;
                padding: 10px 8px 8px 10px;
                background-color: #0000003b;;
                margin: 1px;
                cursor: pointer;
                position: relative;

                img {
                    position: absolute;
                    right: -25px;
                    width: 20px;
                    height: 20px;
                }

                .stem-types {
                    flex: 1;
                }
            }

            .add-stems {
                display: flex;
                background-color: white;
                color: black;
                text-align: center;
                padding: 10px;
            }
        }
        .songs {
            display: flex;
            flex-direction: column;

            .song.selected {
                background-color: #ffffff2e;
            }

           .song {
                padding: 10px 2px 2px 10px;
                cursor: pointer;
                display: flex;
                height: 40px;
                align-items: flex-start;
                font-size: 10pt;
                cursor: pointer;

                div {
                    margin-right: 10px;
                }

                .song-artist {
                    color: #9c9e94;
                }

                .song-bpm, .song-key {
                    align-items: flex-start;
                    background-color: white;
                    color: black;
                    padding-left: 10px;
                    padding-right: 10px;
                    border-radius: 8px;
                }
            }
        }
        
        .header {
            border-bottom: 1px solid white;
        }

        .samples {
            margin-top: 20px;
            .sample:hover {
                .delete {
                    opacity: 1;
                }
            }
            .sample {
                display: flex;
                padding: 10px 8px 8px 10px;
                background-color: #0000003b;;
                margin: 1px;
                max-width: 70%;

               .name {
                    font-size: 10pt;
                    height: 18px;
                    line-height: 18px;
                    width: 150px;
                    overflow: hidden;
                }

                .delete {
                    cursor: pointer;
                    opacity: 0;
                    margin-left: auto;
                    margin-right: 15px;
                    line-height: 18px;
                    height: 18px;
                }
            }
        }

        .drop-files-here {
            margin-right: 20px;
            display: flex;
            width: 15%;
            height: 100px;

            background-image: linear-gradient(45deg, #7979795c, #d2727200);
            border: 1px solid #e9e9e94d;
            color: white;
            span {
                margin: auto;
            }
        }

        .save-flow {
            margin-top: 20px;
            .save-button.disabled {
                background-image: none;
                background-color: gray;
                opacity: 0.5;
            }
            .save-button.save-configuration {
                width: 160px;
            }
            .save-button {
                margin-top: 15px;
                width: 80px;
                text-align: center;
                border-radius: 12px;
                background-image: linear-gradient(45deg, #5bf3cb, #07ff00);
                box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
                color: black;
                cursor: pointer;
            }
        }
    }
}

.toggle-switch {
    margin-left: 10px;
    display: flex;
    .label {
        height: 18px;
    }
    
    .switch.on {
                background-image: linear-gradient(45deg, #8f78fc, #2500ff);
                
                .dot {
                    margin-left: auto;
                }
    }
    .switch {
        padding: 1px;
        height: 18px;
        display: flex;
        width: 40px;
        border-radius: 12px;
        cursor:  pointer;
        background-color: gray;
        
        .dot {
            background-color: white;
            width: 18px;
            height: 18px;
            border-radius: 18px;
        }
    }

}

