.zpore-control-header.next-leak-controller {
    z-index: 30000000000000 !important;

    .close-btn {
        pointer-events: auto !important;
        cursor: pointer;
        margin-top: 2px;
    }
    .marque-container {
        max-width: 260px;
        overflow: hidden;
        font-size: 6px !important;
        padding: 0px !important;
        height: 12px;
        line-height: 5px;
        margin-bottom: 5px;
        .marque {
            padding: 0px !important;
            padding-left: 15px !important;

            //animation: marque 15s linear infinite;
            white-space: nowrap;
        }
    }
    .load-into-spore {
        border-radius: 12px;
        padding: 10px !important;
    }
    .inner-control {
        .top-header {
            background-color: unset;
        }
    }
}

.zpore-control-header {
    position: fixed;
    z-index: 30;
    width: 100%;
    max-width: 500px;
    top: 15px;
    left: 15px;

    svg {
        pointer-events: none;
    }

    .ens-name {
        color: white;
        margin-left: 5px;
    }

    .inner-control {
        .top-header {
            background-color: #141414;
        }
        .simple-controls {
            position: unset !important;

            .play-btn-wrapper {
                width: 40px;
                height: unset;
                margin: 0px !important;
                padding: 0px;
            }
        }
        border-radius: 13px !important;
        padding: 1px;

        .remixes-btn {
            margin-bottom: 5px;
            margin-top: -8px;
            width: 120px;
            border-bottom: 1px solid transparent;
            cursor: pointer;
            padding-bottom: 0px !important;
        }

        .remixes-btn:hover {
            border-bottom-color: white;
            animation-duration: 1s;
            animation-name: hover-remixes;
        }
        
    }
}

@keyframes hover-remixes {
    from {
        border-bottom-color: transparent;
    }
    to {
        border-bottom-color: white;
    }
}

@media screen and (max-width: 600px) {
    .zpore-control-header {
        top: 0px;
        left: 0px;
    }
}

@keyframes marque {
    0%   { transform: translateX(100%); }
    100% { transform: translateX(-100%); }
}
