.inner-overlay {
    position: relative;
    display: flex;
    flex-direction: column;
    height: 350px;
    
    img {
        margin-left: auto;
        margin-right: auto;
    }
    .circular-progress {
        position: absolute;
        top: 0px;
        left: 0px;
        right: 0px;
        margin-left: auto;
        margin-right: auto;
        transform: translate(6px, -14px);
        
        circle.outter-circle {
            transform: translate(-15px, -10px);
            stroke: #49494930;;
        }
        circle {
            transform: translate(-15px, -10px);
            fill: transparent;
        }
    }
}
