.zpore {
    svg.spore-roots {
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 1;

        path.not-dashed {
            stroke: #7171710d;
            stroke-width: 3px;
            fill: transparent;
            z-index: 1;
        }

        path.dashed {
            z-index: 2;
            stroke: #5454540d;
            stroke-dasharray: 1 1;
            stroke-dashoffset: 100;
            stroke-width: 3px;
            fill: transparent;
        }
    }
}

