.waitlist {
    .email-input {
        border-bottom: 1px solid white;
    }

    .inner-waitlist {
        display: flex;
        flex-direction: column;

        max-width: 80%;
        align-items: flex-start;
        height: 50%;

        .ok-btn.show {
            opacity: 1;
            pointer-events: unset;
        }

        .submitted-text {
            background: linear-gradient(60deg, #00ffd8 0%, #ffe500 40%, #00f7ff 60%, blue 80%, #ff00a1 90%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            display: inline-block;
            -webkit-mask:linear-gradient(-60deg,#000 30%,#0005,#000 70%) right/300% 100%;
            background-repeat: no-repeat;
            display: flex;
            animation: shimmer 2.7s infinite;
            margin-top: 5px;
            padding: 8px;

            svg {
                margin-left: 8px;
                margin-top: 5px;
                path {
                    fill: white;
                }
            }
        }

        .ok-btn {
            opacity: 0;
            pointer-events: none;
            display: flex;
            align-items: flex-start;
            width: 60px;
            color: black;
            padding: 8px;
            text-align: center;
            background-color: #8fed43;
            margin-top: 5px;
            border-radius: 8px;
            
            svg {
                margin-top: 5px;
                margin-left: 5px;
            }
        }
    }
}
