.fullscreen-backdrop {
    position: absolute;
    top: 0px;
    background-color: #00000094;
    backdrop-filter: blur(16px);
    left: 0px;
    right: 0px;
    bottom: 0px;
    width: 100%;
    height: 100%;
    z-index: 1000000000000;
    display: flex;
    
    .fullscreen-gen {
        border-radius: 30px;
        margin: auto;
        height: 500px;
        width: 500px;
        background-color: #00000061;
    }
}


.currently-playing {
    display: flex;
    background-color: rgb(56 43 94 / 70%);
    background-image: linear-gradient(218deg, rgb(17 32 51 / 63%), transparent);
    position: fixed;
    backdrop-filter: blur(8px);
    width: 100%;
    height: 80px;
    z-index: 10000000000;
    color: white;
    bottom: 0px;

    .gen-img {
        margin-left: 15px;
        margin-top: auto;
        margin-bottom: auto;
        width: 80px;
        height: 80px;
    }

    .ens-name {
        margin-top: auto;
        margin-bottom: auto;
        margin-left: 15px;
        margin-right: 15px;
        color: white;
    }

    img.zorb {
        margin-left: auto;
        margin-top: 20px;
        width: 40px;
        height: 40px;
    }
    svg {
        margin-top: 3px;
        circle {
            fill: transparent;
        }
    }
}


@media screen and (max-width: 600px) {
    .currently-playing {
        height: 60px;

        img.zorb {
            margin-top: 10px;
        }

        .gen-img {
            width: 50px;
            height: 50px;
        }

    }
    .fullscreen-backdrop {
        .fullscreen-gen {
            border-radius: 30px;
            margin: auto;
            width: 70%;
            height: unset;
            background-color: black;
        }
    }
}
