.slice-recorder {
    position: absolute;
    left: 40px;
    bottom: 35px;
    width: 70px;
    z-index: 100000;

    path {
        storke: red;
    }

    img {
        z-index: 3;
    }
    .rec-dot {
        display: none;
        position: absolute;
        z-index: 5;
        width: 15px;
        height: 15px;
        border-radius: 15px;
        background-color: red;
        bottom: 30px;
        left: 27px;
    }
}

@media screen and (max-width: 600px) {
   .slice-recorder {
       left: unset;
       left: 10px;
       bottom: 10px;
       width: 45px;
   }
}
