
.zora-modal.mint-completed {
    .modal-content {
        .modal-body {
            flex-direction: column;

            .completed-minting-message {
                font-size: 16pt;
            }

            .mint-links {
                margin-top: 15px;
            }
        }


    }
}
.zora-modal {
    width: 900px;
    min-width: 900px;
    
    .nft-url {
        position: relative;
        background-color: black;
        padding: 5px;
        margin-right: 10px;

        .copy-url {
            position: absolute;
            right: 5px;
            background-color: white;
            top: 0px;
            color: black;
            padding: 4px;
            font-size: 9pt;
            border-radius: 8px;
        }
    }

    .modal-content {
        min-width: 900px;
        width: 900px;
        background-color: #151515eb !important;
        position: relative;

        .progress-bar {
            top: 466px !important;
            left: -10px !important;
            width: 890px !important;
            height: 95px !important;
            opacity: 0.8;
            backdrop-filter: blur(16px);
            background-color: #9ac2fe42 !important;
            z-index: 1000000000000;
            position: absolute !important;
            
            .loading-text {
                font-size: 16pt !important;
                margin-top: 28px;
                color: black;
                margin-left: 25px;
            }

            .progress {
                height: 85px !important;
                backdrop-filter: blur(16px);
                background-image: linear-gradient(to right, #c419c6, #5e5ec5);
            }

            .inner-progress-bar {
                display: flex;
                backdrop-filter: blur(16px);
                height: 85px !important;
                width: 885px !important;
            }
            position: absolute !important;
        }

        .btn-secondary {
            border-radius: 5px;
            color: white;
            background-color: #2673ff;
            background-image: linear-gradient(
                60deg, #fc3277, transparent);
        }

        .modal-header {
            border-bottom-color: transparent !important;
        }

        .modal-footer {
            border-top-color: transparent !important;
        }

        .modal-body {
            display: flex;

            .zora-configuration {
                margin-left: 10px;

                .royalties {
                    margin-top: 10px;
                    .p-box {
                        position: absolute;
                        top: 25px;
                        border-radius: 3px;
                        background-color: #00000047;
                        margin-top: 5px;

                        .volume-box {
                            border-radius: 3px;
                        }

                        .volume-value {
                            position: absolute;
                            left: 40%;
                            top: 2px;
                            pointer-events: none;
                            z-index: 1000;
                            font-size: 10pt;
                        }
                        .volume-indicator {
                            opacity: 1;
                            border-radius: 3px;
                            background-color: #2673ff;
                            background-image: linear-gradient(
                                60deg, #fc3277, transparent);
                        }
                    }
                }

                .alternate-thumbnails {
                    display: flex;
                    flex-wrap: wrap;

                    img.selected {
                        border: 1px solid white;
                    }

                    img {
                        cursor: pointer;
                        border: 1px solid transparent;
                        width: 25px;
                        height: 25px;
                        margin: 1px;
                    }
                }
                .drop-here.loaded {
                    width: 250px;
                    height: 250px;
                    max-width: 250px;
                    max-height: 250px;
                    border: none;

                    img {
                        width: 100%;
                        height: 100%;
                    }

                    span {
                        display: none;
                    }
                }

                .drop-here {
                    margin-top: 10px;
                    border: 1px dashed grey;
                    color: gray;
                    width: 250px;
                    height: 100px;
                    display: flex;

                    span {
                        text-align: center;
                        width: 170px;
                        margin: auto;
                    }
                }

           }
        }
    }
}

@keyframes grow-field{
    from{
        height: 30px;
    }
    to{
        height: 50px;
    }
}

@keyframes grow-textarea-field{
    from{
        height: 30px;
    }
    to{
        height: 80px;
    }
}




.zora-field.text-area.focused {
    animation-duration: 0.2s;
    animation-name: grow-textarea-field;
    height: 80px;
    
    
    textarea {
        height: 50px;
    }
}

.zora-field.focused {
    animation-duration: 0.2s;
    animation-name: grow-field;
    height: 50px;
    
    textarea, input {
        background-color: #00000047 !important;
        cursor: unset;
    }
    .field-name {
        color: gray;
    }
    
}
.zora-field {
    position: relative;
    height: 30px;
    display: flex;
    flex-direction: column;
    
    .field-name {
        position: absolute;
        top: 0px;
        pointer-events: none;
    }
    
    textarea {
        resize: none;
    }
    textarea, input {
        cursor: pointer;
        font-size: 10pt;
        margin-top: auto;
        color: white;
        background-color: transparent;
        box-shadow: unset !important;
        border: none !important;
        outline: none !important;
        width: 250px;
    }
}
 
