.leak-controller {
    position: fixed !important;

    bottom: 0px;
    top: 0px;
    margin-top: auto;
    margin-bottom: auto;
    transform: translate(0px, 35px);
    pointer-events: none;
    left: -20px;
    padding-left: 20px;

    .close-icon {
        pointer-events: auto !important;
        cursor: pointer;
    }
    
    .z-30 {
        display: flex;
        flex-direction: column;
        min-width: 200px;
    }

    .ens-name {
        color: white;
        
    }
}
