.zpore {
    .remixes-button:hover {

        span {
            background: linear-gradient(60deg, rgb(3 238 254) 0%, rgb(133 117 255 / 88%) 25%, rgb(181 60 238) 50%, rgb(255 51 198) 75%, rgb(255 43 177) 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            display: inline-block;
            -webkit-mask:linear-gradient(-60deg,#000 30%,#0005,#000 70%) right/300% 100%;
            background-repeat: no-repeat;
            animation: shimmer 2.5s infinite;
        }
    }

    .remixes-button {
        cursor: pointer;
        position: absolute;
        bottom: 0px;
        left: 0px;
        right: 0px;
        margin-left: auto;
        margin-right: auto;
        width: 160px;
        text-align: center;
        /* margin-left: 30px; */
        padding: 8px;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        z-index: 100000;
        background-color: #262525;
    }
}

@keyframes shimmer {
    100% {-webkit-mask-position:left}
}
